@import './../nulls.scss';

$color-default: #F53855;

.ButtonHover {
  text-wrap: none;
  padding: 10px 40px;
  background: white;
  border:  2px solid white;
  font-weight: 600;
  color: #0B132A;
  &:hover {
    border-radius: 50px;
    border:  2px solid $color-default;
    color: $color-default;
  }
}